import { Component, OnInit } from '@angular/core';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { MenuService } from 'src/app/core/menu.service';
import { IUser } from 'src/app/core/user.model';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  sidebarOpen$ = this.menuService.sidebarOpen$;
  user: IUser;
  constructor(private authUserService: AuthUserService, private menuService: MenuService) { }

  ngOnInit(): void {
    this.user = this.authUserService.user;
  }
}
